export default {
	methods:{
		getOpenId() {
			uni.login({
				success: res => {
					if (res.code) {
						uni.getUserInfo({
							success: res => {
								// console.log('存在code')
							}
						})
						let code = res.code
						uni.request({
							url: 'https://www.finetwm.com/xmRepair/userInfo/login',
							data: {
								code: code
							},
							method: 'GET',
							success: res => {
								// this.openId = res.data.openid
								console.log(res.data.data.openid)
								this.openId = res.data.data.openid
								uni.setStorage({
									key: 'openId',
									data: this.openId,
									success:res=>{
										console.log(res)
										uni.navigateBack()
									}
								})
								// obj.expires_in = Date.now() + res.data.expires_in
							}
						});
					} else {
						console.log('获取用户登录态失败！' + res.errMsg);
					}
				}
			})
		}
	}
}
